import React from "react";
import { useModalStore } from "../../../../../../../../store/potg/modal";
import InGameModalView from "../../modal/InGameModalView";

function InGameButton({ isInGame }: { isInGame: boolean }) {
  const { setVisiblity, setContent } = useModalStore();

  return (
    <div
      className={`flex items-center gap-2 w-fit cursor-pointer ${
        isInGame ? "" : "hidden"
      }`}
      onClick={() => {
        setVisiblity(true);
        setContent(InGameModalView());
      }}
    >
      <div className="flex h-3 w-3">
        <div className="animate-ping h-full w-full rounded-full bg-emerald-400 opacity-75" />
        <div className="absolute rounded-full h-3 w-3 bg-emerald-500" />
      </div>
      <div className="text-xs text-emerald-600">게임 중 {">"}</div>
    </div>
  );
}

export default InGameButton;
