const RateGaugebar = (props: GaugebarData) => {
  let displayColor = "bg-rose-500";

  if (props.value < 50) displayColor = "bg-gray-400";
  else if (props.value < 60) displayColor = "bg-emerald-500";
  else if (props.value < 65) displayColor = "bg-sky-500";
  else if (props.value < 70) displayColor = "bg-rose-500";
  return (
    <div className="flex flex-col gap-1">
      <div className="text-xs text-center">승률 {props.value}%</div>
      <div className="flex h-6 w-20 items-center">
        <Gaugebar max={props.max} value={props.value} color={displayColor} />
      </div>
    </div>
  );
};

